import React, { useContext } from "react";
import { Waypoint } from "react-waypoint";
import { PageContext } from "../../PageContext";

export default function Presentation() {
  const { changeCurrent } = useContext(PageContext);

  return (
    <div id="presentation" className="container container-presentation">
      <Waypoint
        bottomOffset="60%"
        onEnter={() => {
          changeCurrent(2);
        }}
      />
      <div className="txt">
        <p>
          Passionné depuis toujours par le monde de l'informatique et du web,
          j'ai décidé de faire une reconversion professionnelle et de me former
          au métier de développeur web à l'école WebForce3 de Bordeaux.
        </p>

        <p>
          Après avoir obtenu mon certificat de développeur web, je me suis
          spécialisé sur le langage JavaScript et en particulier sur le
          frameworks React.js et certaines de ses librairies.
        </p>

        <p>
          Je continue de me perfectionner sur ce framework en créant
          régulièrement des nouveaux projets que je poste sur mon{" "}
          <a className="link" href="https://github.com/MatthieuFmt">
            GitHub
          </a>
          .
        </p>

        <p>En voici quelques un :</p>
      </div>
    </div>
  );
}
