import React, { useState, useContext } from "react";
import projet1 from "../../assets/projet1.png";
import projet2 from "../../assets/projet2.png";
import projet3 from "../../assets/projet3.png";
import projet4 from "../../assets/projet4.png";
import projet5 from "../../assets/projet5.png";
import { Waypoint } from "react-waypoint";
import { useSpring, animated } from "react-spring";
import { PageContext } from "../../PageContext";

export default function Projects() {
  const [apparition1, setApparition1] = useState(false);
  const [apparition2, setApparition2] = useState(false);
  const [apparition3, setApparition3] = useState(false);
  const [apparition4, setApparition4] = useState(false);
  const [apparition5, setApparition5] = useState(false);

  const animation1 = useSpring({
    opacity: apparition1 ? 1 : 0,
    transform: apparition1 ? "translateY(0)" : "translateY(50%)",
  });
  const animation2 = useSpring({
    opacity: apparition2 ? 1 : 0,
    transform: apparition2 ? "translateY(0)" : "translateY(50%)",
  });
  const animation3 = useSpring({
    opacity: apparition3 ? 1 : 0,
    transform: apparition3 ? "translateY(0)" : "translateY(50%)",
  });
  const animation4 = useSpring({
    opacity: apparition4 ? 1 : 0,
    transform: apparition4 ? "translateY(0)" : "translateY(50%)",
  });
  const animation5 = useSpring({
    opacity: apparition5 ? 1 : 0,
    transform: apparition5 ? "translateY(0)" : "translateY(50%)",
  });

  const { changeCurrent } = useContext(PageContext);

  return (
    <div id="projects" className="container container-projects">
      <Waypoint
        bottomOffset="65%"
        onEnter={() => {
          changeCurrent(3);
        }}
      />
      {/* HIIT */}
      <Waypoint
        bottomOffset="20%"
        onEnter={() => {
          if (!apparition5) {
            setApparition5(true);
          }
        }}
      />
      <animated.div style={animation5} className="project">
        <a href="https://matthieufmt.github.io/hiit/">
          <div className="project-img">
            <img src={projet5} alt="screenshot du projet H.I.I.T" />
            <div className="show">Voir le projet</div>
          </div>
          <div className="project-infos">
            <p>
              Type : <em>Chronomètre HIIT</em>
            </p>
            <p>
              Outils utilisés : <em>React.js, Redux</em>
            </p>
            <p>
              Fonctionalités :{" "}
              <em>
                Choix du nombres d'exercices, tours ,timer. Changement du nom
                des exercices. Play, pause, reset.{" "}
              </em>
            </p>
            <p>
              Hébergé sur : <em>GitHub</em>
            </p>
          </div>
        </a>
      </animated.div>
      {/* e-commerce */}
      <Waypoint
        bottomOffset="25%"
        onEnter={() => {
          if (!apparition4) {
            setApparition4(true);
          }
        }}
      />
      <animated.div style={animation4} className="project">
        <a href="https://e-commerce-nine-swart.vercel.app/">
          <div className="project-img">
            <img src={projet4} alt="screenshot du projet todo list" />
            <div className="show">Voir le projet</div>
          </div>
          <div className="project-infos">
            <p>
              Type : <em>E-commerce</em>
            </p>
            <p>
              Outils utilisés : <em>Next.js</em>
            </p>
            <p>
              Fonctionalités :{" "}
              <em>le front-end d'un site de vente en ligne.</em>
            </p>
            <p>
              Hébergé sur : <em>Vercel</em>
            </p>
          </div>
        </a>
      </animated.div>
      {/* netflix */}
      <Waypoint
        bottomOffset="25%"
        onEnter={() => {
          if (!apparition1) {
            setApparition1(true);
          }
        }}
      />
      <animated.div style={animation1} className="project">
        <a href="https://matthieufmt.github.io/Clone-Netflix/">
          <div className="project-img">
            <img src={projet1} alt="screenshot du projet netflix" />
            <div className="show">Voir le projet</div>
          </div>
          <div className="project-infos">
            <p>
              Type : <em>Intégration</em>
            </p>
            <p>
              Outils utilisés : <em>React.js, Sass</em>
            </p>
            <p>
              Fonctionalités : <em>Reproduction du site Netflix responsive.</em>
            </p>
            <p>
              Hébergé sur : <em>GitHub</em>
            </p>
          </div>
        </a>
      </animated.div>
      {/* blog firebase */}
      <Waypoint
        bottomOffset="25%"
        onEnter={() => {
          if (!apparition2) {
            setApparition2(true);
          }
        }}
      />
      <animated.div style={animation2} className="project">
        <a href="https://matthieufmt.github.io/firebase-blog/">
          <div className="project-img">
            <img src={projet2} alt="screenshot du projet météo" />
            <div className="show">Voir le projet</div>
          </div>
          <div className="project-infos">
            <p>
              Type : <em>Blog</em>
            </p>
            <p>
              Outils utilisés : <em>React.js, Firebase</em>
            </p>
            <p>
              Fonctionalités :{" "}
              <em>
                Inscription/Connexion à un compte firebase, création d'article
                et la possibilité de les modifier, sauvegarde dans le
                localstorage.{" "}
              </em>
            </p>
            <p>
              Hébergé sur : <em>GitHub</em>
            </p>
          </div>
        </a>
      </animated.div>
      {/* blog */}
      <Waypoint
        bottomOffset="25%"
        onEnter={() => {
          if (!apparition3) {
            setApparition3(true);
          }
        }}
      />
      <animated.div style={animation3} className="project">
        <a href="https://network-nerm-app.herokuapp.com/">
          <div className="project-img">
            <img src={projet3} alt="screenshot du projet blog" />
            <div className="show">Voir le projet</div>
          </div>
          <div className="project-infos">
            <p>
              Type : <em>Réseau social</em>
            </p>
            <p>
              Outils utilisés : <em>React.js, Redux, Node.js, MongoDB</em>
            </p>
            <p>
              Fonctionalités :{" "}
              <em>
                Inscription/Connexion, Cryptage des mots de passe, génération de
                token, ajout de posts (images, vidéos), commentaires, likes,
                ajout de contact, photo de profil.{" "}
              </em>
            </p>
            <p>
              Hébergé sur : <em>Heroku</em>
            </p>
          </div>
        </a>
      </animated.div>

      <Waypoint
        bottomOffset="99%"
        onEnter={() => {
          changeCurrent(4);
        }}
      />
    </div>
  );
}
