import React, { useContext } from "react";
import { PageContext } from "../../PageContext";
import { Waypoint } from "react-waypoint";
import { useTrail, animated } from "react-spring";
import { v4 } from "uuid";

export default function Home() {
  const { changeCurrent } = useContext(PageContext);
  const cardSkill = [
    "Redux",
    "GitHub",
    "Git",
    "Sass",
    "Bootstrap",
    "VS-Code",
    "React-springs",
    "npm",
    "Firebase",
    "MongoDB",
    // "Type-Script",
  ];

  const trail = useTrail(cardSkill.length, {
    from: {
      opacity: 0,
    },
    to: {
      opacity: 1,
    },
  });

  return (
    <div id="home" className="container container-home">
      <Waypoint
        bottomOffset="90%"
        onEnter={() => {
          changeCurrent(1);
        }}
      />
      <div className="content-home">
        <div className="name">
          <h2>Matthieu FRÉMONT</h2>
          <h1>Développeur JavaScript</h1>
        </div>

        <div className="skills">
          <ul className="secondary-skills">
            {trail.map((cardStyle, index) => {
              return (
                <animated.li
                  key={v4()}
                  style={cardStyle}
                  className="card-skill"
                >
                  {cardSkill[index]}
                </animated.li>
              );
            })}
          </ul>
          <ul className="skill">
            <li>
              <h3>HTML</h3>
              <div className="level html"></div>
            </li>
            <li>
              <h3>CSS</h3>
              <div className="level css"></div>
            </li>
            <li>
              <h3>Javascript</h3>
              <div className="level js"></div>
            </li>
            <li>
              <h3>React.js</h3>
              <div className="level react"></div>
            </li>
            <li>
              <h3>Next.js</h3>
              <div className="level next"></div>
            </li>
            <li>
              <h3>Node.js</h3>
              <div className="level node"></div>
            </li>
          </ul>
        </div>
      </div>
      <Waypoint
        bottomOffset="70%"
        onEnter={() => {
          changeCurrent(1);
        }}
      />
    </div>
  );
}
