import React, { useContext } from "react";
import { PageContext } from "../../PageContext";

export default function NavBar() {
  const { current } = useContext(PageContext);

  return (
    <nav>
      <a href="#home" className={current === 1 ? "nav-active" : "nav-link"}>
        Accueil
      </a>
      <a
        href="#presentation"
        className={current === 2 ? "nav-active" : "nav-link"}
      >
        Présentation
      </a>
      <a href="#projects" className={current === 3 ? "nav-active" : "nav-link"}>
        Projets
      </a>
      <a href="#form" className={current === 4 ? "nav-active" : "nav-link"}>
        Contact
      </a>
      <a
        href={process.env.PUBLIC_URL + "/cv.pdf"}
        download
        className="download"
      >
        CV <i className="fas fa-file-download"></i>
      </a>
    </nav>
  );
}
